.search-bar-container{max-width:400px;display:block}.player-search-input{height:50px;width:300px;border-radius:3px;position:relative}.player-search-input.active .search-bar-results{opacity:1;display:block}#search-bar{box-sizing:border-box;height:50px;width:300px;padding:15px 15px;border-radius:5px;outline:none;color:#bb0d3e;font-size:20px}.search-bar-results{padding-top:15px;padding-bottom:15px;max-height:195px;overflow-y:hidden;overflow:hidden;background:white;box-sizing:border-box;color:#bb0d3e;opacity:0;display:none;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2)}.search-bar-results li{list-style:none;padding:10px 25px;width:100%;cursor:default;border-radius:2px;z-index:3;margin-bottom:2px;box-sizing:border-box;font-size:20px}.search-bar-results li:nth-child(4){margin-bottom:15px}.search-bar-results li:hover{background:#057aff;color:wheat}
