.teams{position:relative;display:block;cursor:pointer;height:80px;text-align:center;line-height:80px}.dropdown-content-container{display:none;position:absolute;z-index:1;background-color:#fff;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);min-width:780px;right:-400px;top:76px;padding-bottom:15px;cursor:auto}.dropdown-content-container div{display:inline-block;height:220px;width:250px}.dropdown-content-container div p{font-size:20px;color:gray;line-height:3;text-align:start;padding-left:10px;cursor:default}.teams:hover>p{color:white}.teams:hover .dropdown-content-container{display:block}.dropdown-content-container li{list-style:none;font-size:20px;line-height:1.5;padding-left:10px;padding-right:40px;text-align:start}.dropdown-content-container li:hover{background-color:#057aff;color:wheat;border-radius:5px;cursor:pointer}
