.main-content-container{height:783px}.player-info-container{display:flex;flex-direction:column;line-height:1.5;width:500px;box-shadow:0px 8px 16px 0px #000;margin:auto;margin-top:20px}.player-info-container li{list-style:none;color:#bb0d3e;font-size:20px;font-size:20px;text-align:center}.Ranking-Percentile-button-container{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:auto}#percentage-ranking-toggle-button{opacity:0;border-radius:50%;border-radius:50%;height:75px;width:92px;text-align:center;background:#041e42;color:white;border:2px solid #bb0d3e;font-style:italic;font-size:16px;font-weight:800;cursor:pointer}#percentage-ranking-toggle-button:hover{color:#BF0D3E;background:#031424}#percentage-ranking-toggle-button:active{transform:scale(0.95)}#percentage-ranking-toggle-button:focus{outline:none}.chart-container{display:none;flex-direction:column;margin:20px}.team-players-div{display:flex;flex-direction:column}.team-players-div h1{margin:auto;font-size:30px;text-align:center;color:#041e42;padding-top:20px}.team-players-div .player-ul{display:flex;justify-content:space-evenly;font-size:20px;padding:20px;color:#bb0d3e;border-bottom:2px solid  #C0C0BB}.team-players-div ul li{text-align:center;line-height:1.5;margin-left:10px;margin-right:10px}.team-players-div .player-ul:hover{background-color:#057aff;color:wheat;border-radius:5px;cursor:pointer}
